<template>
  <v-footer app>
    <v-spacer />
    <span class="dark-toggle mr-3 pointer" @click.stop="darkToggle">
      modo escuro:
      <a>{{ $vuetify.theme.dark ? "on" : "off" }}</a>
    </span>
    <span class="mr-3"
      >{{ $store.state.appName }} &copy; {{ year }} v{{ version }}</span
    >
  </v-footer>
</template>

<script>
import LocalStorage from "@/utils/helpers/localStorage";

export default {
  name: "restricted-footer",
  computed: {
    year: () => new Date().getFullYear(),
    version: () => process.env.VUE_APP_VERSION,
  },
  methods: {
    async darkToggle() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      await LocalStorage.set("darkMode", this.$vuetify.theme.dark);
    },
  },
};
</script>

<style scoped>
.pointer {
  cursor: pointer;
}
</style>
